html, body {
  margin: 0;
  padding: 0;
}

.list-items {
  font-size: small;
  margin: 0;
}

.footerbar {
  position: fixed;
  bottom: 0; 
  width: 90%;
}

.btn-circle.btn-xl { 
  width: 70px; 
  height: 70px; 
  padding: 10px 16px; 
  border-radius: 35px; 
  font-size: 12px; 
  text-align: center; 
} 

.list-group {
  margin-bottom: 80px; 
}

.infoblocks {
  display: flex;
}

.info-btns {
  margin: 5px;
}

.platform-container {
  display: flex;
  justify-content: space-around;
}

.signup-platforms {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.sign-btn {
  display:flex;
  justify-content: center;
}

.platforms-list {
  width: 45%;
}

.signup-form p {
  justify-content: center;
}

.search-input {
  width: 77%;
}

.card-img-top {
 width: 95%;
}

.add-info-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.platform-dropdown {
  width: 50%;
}

h1, h2, h3, h4, h5 {
  text-align: center;
}

.homeContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 400px;
}